import React from 'react';
import { connect } from 'react-redux';
import * as PropTypes from 'prop-types';
import Tooltip from '@material-ui/core/Tooltip';
import { withStyles } from '@material-ui/core/styles';

import { actions } from 'session/store/actions';
import { currentUserSelector, beVersionSelector, beLicenceSelector, beHealthSelector } from 'session/store/state';
import { Navigation, UserMenu, Compile } from './components';
import pkg from '../../../../../package.json';

import MasLogo from '../../../../assets/images/mas_logo_white.svg';
import VenditaLogo from '../../../../assets/images/vendita_logo_white.svg';
import './sidebar.local.scss';

export class SidebarContainer extends React.PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      postgresStatus: null,
      masDaemonStatus: null,
      uptime1Minute: null,
      daysUntilExpire:null,
      licenseStatus:null,
      expirationDate:null


    };
  }
  componentDidMount() {
    this.props.getBeVersion();
    this.props.getBeLicence();
    this.props.getBeHealth();
 
  }

     titleCase(str) {
    let splitStr = str.toLowerCase().split(' ');
    for (let i = 0; i < splitStr.length; i++) {
        // You do not need to check if i is larger than splitStr length, as your for does that for you
        // Assign it back to the array
        splitStr[i] = splitStr[i].charAt(0).toUpperCase() + splitStr[i].substring(1);     
    }
    // Directly return the joined string
    return splitStr.join(' '); 
 }
  
  render() {
    const running = {
      height: "10px",
      width: "10px",
      backgroundColor: "rgb(79, 219, 70)",
      borderRadius: "50%",
      position: "relative",
      left: "6px",
      top: "20px"
    }

    const statusSpan = {
      color: "#fff",
      fontSize: "11px",
      marginLeft: "14px",
      textShadow: "1px 1px 1px #3765af3b",
      position: "relative",
      top: "-2px"
    }
    const stopped = {
      height: "10px",
      width: "10px",
      backgroundColor: "red",
      borderRadius: "50%",
      position: "relative",
      left: "6px",
      top: "20px"
    }


    if (this.props.beHealth) {
      let data = this.props.beHealth;
      this.setState({
     postgresStatus: this.props.beHealth.masSystemsMonitor.postgresStatus,
     masDaemonStatus: this.props.beHealth.masSystemsMonitor.masDaemonStatus,
      uptime1Minute:this.props.beHealth.masSystemsMonitor.uptime1Minute+','+this.props.beHealth.masSystemsMonitor.uptime5Minutes+','+this.props.beHealth.masSystemsMonitor.uptime15Minutes

      })


    }

    if (this.props.beLicence) {
      let data = this.props.beLicence;
      this.setState({
        daysUntilExpire:data.licensed_feature[1].daysUntilExpire,
        expirationDate:data.licensed_feature[1].expirationDate,
        licenseStatus:data.licensed_feature[1].licenseStatus,
        description:data.licensed_feature[1].description
        
      })

    }
   

    return (
      
      <div className={this.props.className}>
        <div className="logo-container">
          <img className="vendita-logo img-responsive" src={VenditaLogo} alt="Vendita logo" />
          <LightTooltip
            placement="right"
            title={
              <>
                <div>
                  <span style={{ paddingRight: '2px' }}>UI </span>version:&nbsp;
                  {pkg.version}
                </div>
                <div>
                  <span>BE </span>version:&nbsp;
                  {this.props.beVersion}
                </div>

                <div>
                <span>BE </span>License: {(this.state.licenseStatus ? 'MAS Lite' : 'MAS Full')} <br />
                  Expiration Date: {this.state.expirationDate} <br />
                  Days Until Expire: {this.state.daysUntilExpire} <br />
                  
                </div>
              </>
            }
          >
            <img className="mas-logo img-responsive" src={MasLogo} alt="MAS logo" />
          </LightTooltip>
        </div>
        <div className="nav-area">
          <div style={{ position: "relative",
    bottom: "17px",
    right: "2px"}}>
          {this.state.masDaemonStatus &&
            <div style={{top:"14px",position:"relative"}}>
              <div style={running}>
                <span style={statusSpan}><a target="_blank" href="/masmon.html">MAS</a></span>
              </div>
            </div>
          }


          {!this.state.masDaemonStatus && 
            <div style={{top:"14px",position:"relative"}}>
            <div style={stopped}>
                <span style={statusSpan}><a target="_blank" href="/masmon.html">MAS</a></span>
              </div>
            </div>

          }


          {this.state.postgresStatus &&
            <div style={{top:"18px",position:"relative"}}>
            <div style={running}>
                <span style={statusSpan}><a target="_blank" href="/masmon.html">Postgres</a></span>
              </div>
            </div>
          }
          {!this.state.postgresStatus &&
            <div style={{top:"18px",position:"relative"}}>
            <div style={stopped}>
                <span style={statusSpan}><a target="_blank" href="/masmon.html">Postgres</a></span>
              </div>
            </div>
          }

{this.state.uptime1Minute && 
<div style={{top:"23px",position:"relative"}}>
            <div style={running}>
        <span style={statusSpan}><a target="_blank" href="/masmon.html">Load {this.state.uptime1Minute && this.state.uptime1Minute}</a></span>
              </div>
            </div>
  }


{!this.state.uptime1Minute && 
<div style={{top:"20px",position:"relative"}}>
            <div style={stopped}>
        <span style={statusSpan}><a target="_blank" href="/masmon.html">Load {this.state.uptime1Minute && this.state.uptime1Minute}</a></span>
              </div>
            </div>
  }
            </div>
          <Navigation />
        </div>
        <div className="bottom">
          <Compile />
          <UserMenu user={this.props.currentUser.get('name')} />
        </div>
      </div>
    );
  }
}

SidebarContainer.propTypes = {
  className: PropTypes.string,
  currentUser: PropTypes.object.isRequired
};

export const mapStateToProps = state => ({
  currentUser: currentUserSelector(state),
  beVersion: beVersionSelector(state),
  beLicence: beLicenceSelector(state),
  beHealth: beHealthSelector(state)
});
export const mapDispatchToProps = dispatch => ({
  getBeVersion: data => dispatch(actions.getBeVersion.dispatchLoading(data)),
  getBeLicence: data => dispatch(actions.getBeLicence.dispatchLoading(data)),
  getBeHealth: data => dispatch(actions.getBeHealth.dispatchLoading(data))

});

SidebarContainer.defaultProps = {
  className: 'sidebar'
};

export default connect(mapStateToProps, mapDispatchToProps)(SidebarContainer);



const LightTooltip = withStyles(() => ({
  tooltip: {
    color: '#fff',
    backgroundColor: '#787E89',
    letterSpacing: '0.4px',
    fontSize: '12px',
    lineHeight: '16px',
    fontFamily: 'Open Sans'
  },


}))(Tooltip);

