import { put, takeEvery } from '@redux-saga/core/effects';

import { CONSTANTS } from 'config';
import { actions } from 'session/store/actions';
import { RestClient } from 'shared/lib/rest';

function* getBeVersion() {
  try {
    const response = yield RestClient.get(CONSTANTS.BASE.ENDPOINTS.BE_VERSION);
    yield put(actions.getBeVersion.dispatchSuccess(response.data));
  } catch (errors) {
    yield put(actions.getBeVersion.dispatchFail());
  }
}

function* getBeLicence() {
  try {
    const response = yield RestClient.get(CONSTANTS.BASE.ENDPOINTS.BE_LICENCE);
    yield put(actions.getBeLicence.dispatchSuccess(response.data));
  } catch (errors) {
    yield put(actions.getBeLicence.dispatchFail());
  }
}

function* getBeHeath() {
  try {
    const response = yield RestClient.get(CONSTANTS.BASE.ENDPOINTS.BE_HEALTH);
    yield put(actions.getBeHealth.dispatchSuccess(response.data));
  } catch (errors) {
    yield put(actions.getBeHealth.dispatchFail());
  }
}

export function* BeLicenceEffect() {
  yield takeEvery(actions.getBeLicence.getLoadingAction(), getBeLicence);
}



export function* BeHealthEffect() {
  yield takeEvery(actions.getBeHealth.getLoadingAction(), getBeHeath);
}



export function* BeVersionEffect() {
  yield takeEvery(actions.getBeVersion.getLoadingAction(), getBeVersion);
}
