import React from 'react';
import moment from 'moment';

import SvgIcon from '@material-ui/core/SvgIcon';
import Icon from '@mdi/react';
import { mdiCheck, mdiPlayCircle } from '@mdi/js';
import Tooltip from '@material-ui/core/Tooltip';
import { withStyles } from '@material-ui/core/styles';

import { CircleImg, NumberLabel, StringLabel } from 'shared/components';
import { Paper } from 'shared/components/layout';

import './UsedOptions.local.scss';
import Checkbox from '@material-ui/core/Checkbox';
import { Button } from '../../../shared/components/controls';

export class UsedOptions extends React.PureComponent {
  selectedForComparison = {};

  allSids = [];

  constructor(props) {
    super(props);

    const selectedServer = this.props.servers[0];
    const totalInstalledOptions = selectedServer.sids.reduce((acc, sid) => acc + sid.totalInstalledOptions, 0);

    this.allSids = selectedServer.sids || [];
    this.state = {
      visibleSids: this.allSids,
      selectedForComparison: {},
      inComparison: false,
      totalInstalledOptions
    };
  }

  optionType(type) {
    if (type.value === 1) {
      return (
        <LightTooltip
          placement="top"
          title={
            <>
              <span style={{ fontWeight: '600' }}>{type.name}:&nbsp;</span>
              <span>Installed</span>
            </>
          }
        >
          <SvgIcon className="installed">
            <Icon path={mdiCheck} />
          </SvgIcon>
        </LightTooltip>
      );
    }

    return null;
  }

  updateSelection(isChecked, sid) {
    if (isChecked) {
      this.selectedForComparison[`${sid.sid}_${sid.collDate}`] = true;
    } else {
      delete this.selectedForComparison[`${sid.sid}_${sid.collDate}`];
    }
    this.setState({ selectedForComparison: this.selectedForComparison });
    this.forceUpdate();
  }

  compareSelected() {
    const visibleSids = this.allSids.filter((sid) => this.selectedForComparison[`${sid.sid}_${sid.collDate}`]);
    this.setState({ visibleSids, inComparison: true });
  }

  resetSelected() {
    this.selectedForComparison = {};
    this.setState({ visibleSids: this.allSids, inComparison: false, selectedForComparison: this.selectedForComparison });
  }

  render() {
    const { visibleSids, selectedForComparison, inComparison } = this.state;

    return (
      <Paper style={{ height: 'calc(100% - 192px)', padding: '16px' }}>
        <Paper.Header style={{ alignItems: 'center', marginBottom: '24px' }}>
          <div className="grid-header">
            <div className="options-installed">
              <CircleImg
                image={
                  <SvgIcon>
                    <Icon path={mdiPlayCircle} />
                  </SvgIcon>
                }
              />
              <NumberLabel number={this.state.totalInstalledOptions} />
              <StringLabel text="Options installed" />
            </div>
            {
              (Object.keys(selectedForComparison).length > 0 && !inComparison) &&
                <Button style={{ color: '#03A9F4' }} onClick={() => this.compareSelected()}>
                  Compare
                </Button>
            }
            {
              inComparison &&
                <Button style={{ color: '#03A9F4' }} onClick={() => this.resetSelected()}>
                  Compare all
                </Button>
            }
          </div>
        </Paper.Header>
        <Paper.Content scrollable={false}>
          <div className="grid">
            <div className="column header left" />
            <div className="column header aa">
              <LightTooltip placement="top" title="Advanced Analytics">
                <span>AA</span>
              </LightTooltip>
            </div>
            <div className="column header ac">
              <LightTooltip placement="top" title="Advanced Compression">
                <span>AC</span>
              </LightTooltip>
            </div>
            <div className="column header adg">
              <LightTooltip placement="top" title="Active Data Guard">
                <span>ADG</span>
              </LightTooltip>
            </div>
            <div className="column header as">
              <LightTooltip placement="top" title="Advanced Security">
                <span>AS</span>
              </LightTooltip>
            </div>
            <div className="column header dv">
              <LightTooltip placement="top" title="Database Vault">
                <span>DV</span>
              </LightTooltip>
            </div>
            <div className="column header imd">
              <LightTooltip placement="top" title="In-Memory Database">
                <span>IMD</span>
              </LightTooltip>
            </div>
            <div className="column header mt">
              <LightTooltip placement="top" title="Multitenant">
                <span>MT</span>
              </LightTooltip>
            </div>
            <div className="column header ol">
              <LightTooltip placement="top" title="OLAP">
                <span>OL</span>
              </LightTooltip>
            </div>
            <div className="column header p">
              <LightTooltip placement="top" title="Partitioning (User)">
                <span>P</span>
              </LightTooltip>
            </div>
            <div className="column header aa">Collection Date</div>
            <div className="column header aa">Select</div>

            {visibleSids.map((sid, index) => {
              return (
                <div key={`${sid.sid}-${index}`} className="row">
                  <div className="column left">{sid.sid}</div>
                  <div className="column">
                    {this.optionType({ name: 'Advanced Analytics', value: sid.aa_count })}
                  </div>
                  <div className="column">
                    {this.optionType({ name: 'Advanced Compression', value: sid.ac_count })}
                  </div>
                  <div className="column">
                    {this.optionType({ name: 'Active Data Guard', value: sid.adg_count })}
                  </div>
                  <div className="column">
                    {this.optionType({ name: 'Advanced Security', value: sid.as_count })}
                  </div>
                  <div className="column">
                    {this.optionType({ name: 'Database Vault', value: sid.dv_count })}
                  </div>
                  <div className="column">
                    {this.optionType({ name: 'In-Memory Database', value: sid.in_mem_db_count })}
                  </div>
                  <div className="column">
                    {this.optionType({ name: 'Multitenant', value: sid.mt_count })}
                  </div>
                  <div className="column">{this.optionType({ name: 'OLAP', value: sid.olap_count })}</div>
                  <div className="column">
                    {this.optionType({ name: 'Partitioning (User)', value: sid.part_count })}
                  </div>
                  <div className="column">
                    <LightTooltip placement="bottom" title={moment(sid.collDate).format('HH:mm:ss')}>
                      <span>{moment(sid.collDate).format('MMM DD YYYY')}</span>
                    </LightTooltip>
                  </div>
                  <div className="column">
                    <Checkbox
                      onChange={(event) => this.updateSelection(event.target.checked, sid)}
                      size="small"
                      color="primary"
                      checked={!!selectedForComparison[`${sid.sid}_${sid.collDate}`]}
                      classes={{ root: 'checkbox-custom' }}
                    />
                  </div>
                </div>
              );
            }).sort()}
          </div>
        </Paper.Content>
      </Paper>
    );
  }
}
/*
{(sid.rac_count ? optionType({ name: 'Partitioning (User)', value: sid.part_count }) : moment(sid.collDate).format('MMM DD YYYY'))}
*/
const LightTooltip = withStyles(() => ({
  tooltip: {
    color: '#fff',
    backgroundColor: '#787E89',
    letterSpacing: '0.4px',
    fontSize: '12px',
    lineHeight: '16px',
    fontFamily: 'Open Sans'
  }
}))(Tooltip);
