export function mapAllServers(entries) {
  const serversInfo = entries.reduce((acc, entry) => {
    const entryInfo = {
      name: entry.srvname,
      sid: entry.sid,
      collDate: entry.collDate,
      nrCores: entry.Ncore,
      totalDBs: 1,
      eelic_count: entry.eelic_count,
      selic_count: entry.selic_count,
      sids: [entry],
      installedOptions: {
        aa_count: entry.aa_count ? 1 : 0,
        ac_count: entry.ac_count ? 1 : 0,
        as_count: entry.as_count ? 1 : 0,
        dv_count: entry.dv_count ? 1 : 0,
        mt_count: entry.mt_count ? 1 : 0,
        adg_count: entry.adg_count ? 1 : 0,
        rac_count: entry.rac_count ? 1 : 0,
        olap_count: entry.olap_count ? 1 : 0,
        part_count: entry.part_count ? 1 : 0,
        in_mem_db_count: entry.in_mem_db_count ? 1 : 0
      }
    };

    const totalInstalledOptions = Object.values(entryInfo.installedOptions).reduce((a, b) => a + b);
    entryInfo.sids = [{ ...entry, totalInstalledOptions }];
    entryInfo.totalInstalledOptions = totalInstalledOptions;

    acc.push(entryInfo);
    return acc;
  }, []);

  return groupServers(serversInfo).sort((a, b) => a.nrCores - b.nrCores);
}

export function mapVirtualServers(entries) {
  const serversInfo = entries.reduce((acc, entry) => {
    const index = acc.findIndex(sv => sv.vcenterservername === entry.vcenterservername);

    if (index > -1) {
      if (
        entry.vmname !== null ||
        entry.vmguesthostname !== null ||
        entry.vmguestos !== null ||
        entry.vmipaddress !== null
      ) {
        acc[index].hosts_nr += 1;
      }
    } else {
      acc.push({
        vcenterservername: entry.vcenterservername,
        cpu: entry.cpu,
        cpucores: entry.cpucores,
        hosts_nr:
          entry.vmname !== null ||
          entry.vmguesthostname !== null ||
          entry.vmguestos !== null ||
          entry.vmipaddress !== null
            ? 1
            : 0
      });
    }
    return acc;
  }, []);

  return serversInfo;
}

function groupServers(servers) {
  return Object.values(servers.reduce((acc, entry) => {
    if (!acc[entry.name]) {
      acc[entry.name] = entry;
    } else {
      const allSids = [...acc[entry.name].sids, ...entry.sids];
      acc[entry.name] = entry;
      acc[entry.name].sids = allSids;
    }
    return acc;
  }, {}));
}
