import React from 'react';

import SvgIcon from '@material-ui/core/SvgIcon';
import Icon from '@mdi/react';
import { mdiStarCircleOutline } from '@mdi/js';

import { CircleImg, NumberLabel, StringLabel } from 'shared/components';
import { Overflow, Paper } from 'shared/components/layout';

import { OptionsList } from './components/OptionsList/OptionsList';
import { OptionsChart } from './components/OptionsChart/OptionsChart';

import './OptionsTile.local.scss';

export const OptionsTile = ({ servers }) => {
  function groupChartServers(serversToGroup) {
    return Object.values(serversToGroup.reduce((acc, entry) => {
      const serverKey = `${entry.name}_${entry.sid}`;
      if (!acc[serverKey] || acc[serverKey].collDate < entry.collDate) {
        acc[serverKey] = entry;
      }
      return acc;
    }, {}));
  }

  function prepareOptions(filteredServers) {
    return [
      {
        name: 'Advanced Analytics',
        value: filteredServers.reduce((acc, sv) => {
          return acc + sv.installedOptions.aa_count;
        }, 0),
        color: '#176BA0',
        secondaryColor: '#E8F9F5',
        label: 'AA'
      },
      {
        name: 'Advanced Compression',
        value: filteredServers.reduce((acc, sv) => {
          return acc + sv.installedOptions.ac_count;
        }, 0),
        color: '#1AC9E6',
        secondaryColor: '#e8fafc',
        label: 'AC'
      },
      {
        name: 'Advanced Security',
        value: filteredServers.reduce((acc, sv) => {
          return acc + sv.installedOptions.as_count;
        }, 0),
        color: '#6DF0D2',
        secondaryColor: '#f0fdfb',
        label: 'AS'
      },
      {
        name: 'Database Vault',
        value: filteredServers.reduce((acc, sv) => {
          return acc + sv.installedOptions.dv_count;
        }, 0),
        color: '#7D3AC1',
        secondaryColor: '#f2ebf9',
        label: 'DV'
      },
      {
        name: 'Multitenant',
        value: filteredServers.reduce((acc, sv) => {
          return acc + sv.installedOptions.mt_count;
        }, 0),
        color: '#EA7369',
        secondaryColor: '#fbedf7',
        label: 'MT'
      },
      {
        name: 'Active Data Guard',
        value: filteredServers.reduce((acc, sv) => {
          return acc + sv.installedOptions.adg_count;
        }, 0),
        color: '#1DE4BD',
        secondaryColor: '#e8fcf8',
        label: 'ADG'
      },
      {
        name: 'Real Application Clusters',
        value: filteredServers.reduce((acc, sv) => {
          return acc + sv.installedOptions.rac_count;
        }, 0),
        color: '#EF7E32',
        secondaryColor: '#fdf2eb',
        label: 'RAC'
      },
      {
        name: 'OLAP',
        value: filteredServers.reduce((acc, sv) => {
          return acc + sv.installedOptions.olap_count;
        }, 0),
        color: '#F0A58F',
        secondaryColor: '#fdf6f4',
        label: 'OL'
      },
      {
        name: 'Partitioning (User)',
        value: filteredServers.reduce((acc, sv) => {
          return acc + sv.installedOptions.part_count;
        }, 0),
        color: '#C02323',
        secondaryColor: '#f9e9e9',
        label: 'P'
      },
      {
        name: 'In-Memory Database',
        value: filteredServers.reduce((acc, sv) => {
          return acc + sv.installedOptions.in_mem_db_count;
        }, 0),
        color: '#DB4CB2',
        secondaryColor: '#fbedf7',
        label: 'IMD'
      }
    ];
  }

  const groupedServers = groupChartServers(servers);
  const chartOptions = prepareOptions(groupedServers);

  const total = groupedServers.reduce((acc, sv) => {
    return acc + sv.totalInstalledOptions;
  }, 0);

  return (
    <Paper style={{ height: 'calc(100% - 192px)', padding: '16px' }}>
      <Paper.Header style={{ alignItems: 'center', marginBottom: '24px' }}>
        <CircleImg
          image={
            <SvgIcon>
              <Icon path={mdiStarCircleOutline} />
            </SvgIcon>
          }
        />
        <NumberLabel number={total} />
        <StringLabel text="Options installed" />
      </Paper.Header>
      <div className="container">
        <div className="graph-container">
          <OptionsChart data={chartOptions} />
        </div>
        <div className="label-container">
          <Overflow>
            <OptionsList options={chartOptions} />
          </Overflow>
        </div>
      </div>
    </Paper>
  );
};
